.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.95);
    color: #fff;
}

.popup_inner {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* left: 20%;
    right: 20%;
    top: 2vh; */
    height: 100vh;
    
    border-radius: 15px;
    
    margin: 0;
    padding: 10px;
}

